export class User {
     userName: string;
    useremail: string;
     userQuote: string;
     likes: number;
     dislikes: number;
     constructor(name, email, quote) {
         this.userName = name;
         this.useremail = email;
         this.userQuote = quote;
         this.likes = 0;
         this.dislikes = 0;
     }
}
